import React, {useRef} from "react";
import styled, { keyframes } from "styled-components";
import { TypeWriterText, CoverVideo } from "../../components";
import RoundTextBlack from "../../assets/Rounded-Text-White.png";
import sample from "../../assets/background_removed_sample.png"
//import "../../styles/background.css"

const Section = styled.section`
  min-height: ${(props) => `calc(100vh - ${props.theme.navHeight})`};
  width: 100%;
  position: relative;
  /*background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);*/
  /*background-color: ${(props) => props.theme.text};*/
`;

const Container = styled.div`
  width: 50%;
  min-height: 80vh;
  margin: 0 auto;
  /* background-color: lightblue; */

  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 64em) {
    width: 85%;
  }

  @media (max-width: 48em) {
    flex-direction: column-reverse;
    width: 100%;

    & > *:first-child {
      width: 100%;
      margin-top: 2rem;
    }
  }
`;

const Box = styled.div`
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const rotate = keyframes`
  100% {
    transform: rotate(1turn);
  }
`;

const Round = styled.div`
  position: absolute;
  bottom: 2rem;
  right: 90%;
  width: 6rem;
  height: 6rem;

  border: 1px solid ${(props) => props.theme.text};
  border-radius: 50%;

  img {
    width: 100%;
    height: auto;
    animation: ${rotate} 6s linear infinite reverse;
  }

  @media (max-width: 64em) {
    width: 4rem;
    height: 4rem;
    left: none;
    right: 2rem;
    bottom: 100%;
  }
  @media (max-width: 48em) {
    right: 1rem;
  }
`;

const Circle = styled.span`
  width: 3rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: ${(props) => props.theme.fontlg};

  background-color: ${(props) => props.theme.text};
  color: ${(props) => props.theme.body};

  @media (max-width: 64em) {
    width: 2rem;
    height: 2rem;
    font-size: ${(props) => props.theme.fontlg};
  }
`;

/*
function Redirect(){
  window.open("magiceden.io", "_blank");
}
*/


const Home = () => {

  return (
    <Section id="home">
      
      <Container>
        <div id="stars"></div>
        <Box>
          <img src={sample} alt="The Horseshit High Club" />
        </Box>
        
        <TypeWriterText />
        
        <Round>
          <Circle>&#x2193;</Circle>
          <img src={RoundTextBlack} alt="" />
        </Round>
        
      </Container>
      
    </Section>
  );
};

export default Home;
