import React from "react";
import styled from "styled-components";
import GIF from "../assets/Home Video.mp4";
import bannerImage from'../assets/background_removed_sample.png';
//import bannerImage from "../assets/banner.JPG"
const VideoContainer = styled.div`
  width: 100%;

  video {
    width: 100%;
    height: auto;
  }

  @media (max-width: 64em) {
    min-width: 40vh;
  }
`;

const myStyle={
  width:'100%',
  height:'auto',
  };

const CoverVideo = () => {
  return (
    <VideoContainer>
      {/* This place is for home page banner right side video.
      <video src={GIF} type="video/mp4" autoPlay muted loop />
      */}
       <img src={bannerImage} style={myStyle} alt="fireSpot"/>
    </VideoContainer>
  );
};

export default CoverVideo;
