import React from "react";
import styled, { ThemeProvider } from "styled-components";

import Button from "./Button";

import img1 from "../assets/Nfts/sneak.jpg";
import img2 from "../assets/Nfts/sneak2.jpg";
import img3 from "../assets/Nfts/sneak3.jpg";
import img4 from "../assets/Nfts/sneak4.jpg";
import img5 from "../assets/Nfts/sneak5.jpg";
import img6 from "../assets/Nfts/sneak6.jpg";
import { dark } from "../styles/Themes";
import ImgSrc from '../assets/banner.JPG';

const Section = styled.section`
  width: auto;
  height: 50rem;
  position: relative;
  border-top: 2px solid ${(props) => props.theme.text};
  border-top: 2px solid ${(props) => props.theme.text};

  background-image: url(${ImgSrc});
  background-size:     cover;                      /* <------ */
  background-repeat:   no-repeat;

  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;

  @media (max-width: 48em) {
    height: 15rem;
    flex-direction: column;
  }
`;

const ImgContainer = styled.div`
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.2;

  img {
    width: 15rem;
    height: auto;
  }

  @media (max-width: 48em) {
    img {
      width: 10rem;
      height: auto;
    }
  }
`;

const Title = styled.h1`
  font-size: ${(props) => props.theme.fontxxxl};
  color: ${(props) => props.theme.body};
  padding: 1rem 2rem;
  z-index: 10;
  width: 35%;

  text-transform: capitalize;
  text-shadow: 1px 1px 2px ${(props) => props.theme.text};

  @media (max-width: 64em) {
    font-size: ${(props) => props.theme.fontxxl};
    text-align: center;
    width: 40%;
  }
  @media (max-width: 48em) {
    font-size: ${(props) => props.theme.fontxl};
    padding: 2rem 0;
    width: 100%;
  }
`;

const BtnContainer = styled.div`
  width: 35%;
  display: flex;
  justify-content: flex-end;
  font-size: ${(props) => props.theme.fontlg};

  @media (max-width: 48em) {
    width: 100%;
    justify-content: center;
  }
`;

const Banner = () => {
  return (
    <Section>
      {/*}
      <ImgContainer>
        <img src={img1} alt="The Horseshit High Club" />
        <img src={img2} alt="The Horseshit High Club" />
        <img src={img3} alt="The Horseshit High Club" />
        <img src={img4} alt="The Horseshit High Club" />
        <img src={img5} alt="The Horseshit High Club" />
        <img src={img6} alt="The Horseshit High Club" />
      </ImgContainer>
  */}
      <Title>
        Join the <br /> Horseshit High Club
      </Title>
      <ThemeProvider theme={dark}>
        <BtnContainer>
            <a href="https://discord.gg/mCeD6McT"> <Button text="Join Now" link="" /></a>
        </BtnContainer>
      </ThemeProvider>
    </Section>
  );
};

export default Banner;
