import { Button } from "@material-ui/core";
import { useState, useEffect } from "react";
import {
  BrowserRouter,
  Routes, // instead of "Switch"
  Route,
} from "react-router-dom";
import LendingApp from "./lendingApp/LendingApp";
import MintApp from "./mintApp/MintApp";

const mystyle = {
    color: "white",
    backgroundColor: "DodgerBlue",
    padding: "10px",
    fontFamily: "Arial",
    textAlign: "center",
      /* Center horizontally*/
    position: "absolute",
    margin: "0 auto",
    right: "0",
    top: "50"
  };



const lending = (<div><LendingApp> </LendingApp></div>)

function AppManager() {

  return (
    <div>
    <BrowserRouter>
      <Routes>
        <Route exact path="/" element={<LendingApp />}>
        </Route>
        <Route exact path="/mint" element={<MintApp></MintApp>}>
        </Route>
      </Routes>
    </BrowserRouter>
    </div>
  );
}

export default AppManager;
