import React, {useRef} from "react";
import styled, { ThemeProvider, keyframes } from "styled-components";
import Button from "../Button";
import { dark } from "../../styles/Themes";
import sample from "../../assets/background_removed_sample.png"
//import "../../styles/background.css"
import img1 from "../../assets/Nfts/sneak.jpg";
import img2 from "../../assets/Nfts/sneak2.jpg";
import img3 from "../../assets/Nfts/sneak3.jpg";
import img4 from "../../assets/Nfts/sneak4.jpg";
import img5 from "../../assets/Nfts/sneak5.jpg";
import img6 from "../../assets/Nfts/sneak6.jpg";
import img7 from "../../assets/Nfts/sneak7.jpg";

import ETH from "../../assets/Ethereum.svg";

const Section = styled.section`
  min-height: ${(props) => `calc(50vh - ${props.theme.navHeight})`};
  max-height: 150vh;
  width: 100vw;
  position: relative;
  /*background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);*/
  /*background-color: ${(props) => props.theme.text};*/
`;

const Container = styled.div`
  width: 75%;
  margin: 0 auto;
  /* background-color: lightblue; */

  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 70em) {
    width: 85%;
  }

  @media (max-width: 64em) {
    width: 100%;
    flex-direction: column;

    & > *:last-child {
      width: 80%;
    }
  }
  @media (max-width: 40em) {
    & > *:last-child {
      width: 90%;
    }
  }
`;

const Box = styled.div`
  width: 75%;
  height: 100%;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: 40em) {
    min-height: 50vh;
  }
`;

const SubBox = styled.div`
  width: 85%;
  height: 100%;
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  @media (max-width: 40em) {
    min-height: 25vh;
  }
`;

const InfoBox = styled.div`
  width: 100%;
  height: 2vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
  padding:50px;
  border: solid black;
  border-radius: 5px;
  border-image: linear-gradient(to bottom right, #b827fc 0%, #2c90fc 25%, #b8fd33 50%, #fec837 75%, #fd1892 100%);
  border-image-slice: 1;
`;

const Title = styled.h2`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: capitalize;
  color: ${(props) => props.theme.text};
  align-self: center;
  width: 80%;
  margin: 0 auto;

  @media (max-width: 60em) {
    width: 100%;
    text-align: center;
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontxl};
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontlg};
  }
`;

const SubText = styled.p`
  font-size: ${(props) => props.theme.fontlg};
  color: ${(props) => props.theme.text};
  width: 80%;
  align-self: center;
  margin: 1rem auto;
  font-weight: 400;

  @media (max-width: 60em) {
    width: 100%;
    text-align: center;
    font-size: ${(props) => props.theme.fontmd};
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontmd};
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontsm};
  }
`;

const SubTextLight = styled.p`
  font-size: ${(props) => props.theme.fontmd};
  color: ${(props) => `rgba(${props.theme.textRgba}, 0.6)`};
  width: 80%;
  align-self: flex-start;
  margin: 1rem auto;
  font-weight: 400;

  @media (max-width: 60em) {
    width: 100%;
    text-align: center;
    font-size: ${(props) => props.theme.fontsm};
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontsm};
  }
  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontxs};
  }
`;

const ButtonContainer = styled.div`
  width: 80%;
  margin: 1rem auto;
  display: flex;
  align-self: auto;

  @media (max-width: 64em) {
    width: 100%;

    button {
      margin: 0 auto;
    }
  }
`;

const Showcase = styled.section`
  min-height: 35vh;
  width: 100vw;
  /*background-color: ${(props) => props.theme.text};*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  background: transparent;
  

  & > *:first-child {
    animation-duration: 20s;

    @media (max-width: 30em) {
      animation-duration: 15s;
    }
  }
  & > *:last-child {
    animation-duration: 15s;

    @media (max-width: 30em) {
      animation-duration: 10s;
    }
  }
`;

const move = keyframes`
  0% { transform: translateX(100%) }
  100% { transform: translateX(-100%) }
`;

const Row = styled.div`
  /* background-color: lightblue; */
  white-space: nowrap;
  box-sizing: content-box;
  margin: 2rem 0;
  display: flex;

  animation: ${move} linear infinite ${(props) => props.direction};
`;

const ImgContainer = styled.div`
  width: 15rem;
  margin: 0 1rem;
  background-color: ${(props) => props.theme.body};

  border-radius: 20px;
  cursor: pointer;

  @media (max-width: 48em) {
    width: 12rem;
  }
  @media (max-width: 30em) {
    width: 10rem;
  }

  img {
    width: 100%;
    height: auto;
  }
`;

const Details = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 1rem;
  background-color: ${(props) => props.theme.text};
  border: 2px solid ${(props) => `rgba(${props.theme.textRgba}, 0.5)`};
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

  span {
    font-size: ${(props) => props.theme.fontsm};
    color: ${(props) => `rgba(${props.theme.textRgba}, 0.5)`};
    font-weight: 600;
    line-height: 1.5rem;
  }

  h1 {
    font-size: ${(props) => props.theme.fontmd};
    color: ${(props) => props.theme.text};
    font-weight: 600;

    @media (max-width: 30em) {
      font-size: ${(props) => props.theme.fontsm};
    }
  }
`;

const Price = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;

  img {
    width: 1rem;
    height: auto;
  }
`;

const NftItem = ({ img, number = 0, price = 0, passRef }) => {
  let play = (e) => {
    passRef.current.style.animationPlayState = "running";
  };
  let pause = (e) => {
    passRef.current.style.animationPlayState = "paused";
  };

  return (
    <ImgContainer onMouseOver={(e) => pause(e)} onMouseOut={(e) => play(e)}>
      <img src={img} alt="The Horseshit High Club" />
      <Details>
        <div>
          <span>Horseshit</span>
          <br />
          <h1>#{number}</h1>
        </div>

        <div>
          <span>Price</span>
          <Price>
            <img src={ETH} alt="ETH" />
            <h1>{Number(price).toFixed(1)}</h1>
          </Price>
        </div>
      </Details>
    </ImgContainer>
  );
};

const About = () => {

  const Row1Ref = useRef(null);
  const Row2Ref = useRef(null);

  return (
    <Section id="about">
      <Showcase>
        <Row direction="none" ref={Row1Ref}>
          <NftItem img={img1} number={852} price={1} passRef={Row1Ref} />
          <NftItem img={img2} number={123} price={1.2} passRef={Row1Ref} />
          <NftItem img={img3} number={456} price={2.5} passRef={Row1Ref} />
          <NftItem img={img4} number={456} price={2.5} passRef={Row1Ref} />
        </Row>
      </Showcase>
      <Container>
        <Box>
          <Title>
            Welcome To The <br /> Horseshit Club.
          </Title>
          <SubText>
            The Horseshit High Club High Club is a private collection of NFTsunique digital
            collectibles. The Horseshit High Club are stored as tokens on the
            Solana blockchain and hosted on IPFS.
          </SubText>
          <SubTextLight>
            With more than 200+ hand drawn traits, each NFT is unique and comes
            with a membership to an exclusive group of successful investors.
            Join an ambitious ever-growing community with multiple benefits and
            utilities.
          </SubTextLight>
          <SubBox>
            <InfoBox>
              <SubText>
                Supply
              </SubText>
              <SubTextLight>
                6666
              </SubTextLight>
            </InfoBox>
            <InfoBox>
              <SubText>
                Traits
              </SubText>
              <SubTextLight>
                50
              </SubTextLight>
            </InfoBox>
            <InfoBox>
              <SubText>
                Mint Price
              </SubText>
              <SubTextLight>
                0.75
              </SubTextLight>
            </InfoBox>
            <InfoBox>
              <SubText>
                Mint Date
              </SubText>
              <SubTextLight>
                TBA
              </SubTextLight>
            </InfoBox>
          </SubBox>    
          <ButtonContainer>
            <ThemeProvider theme={dark}>
            <a href="https://discord.gg/mCeD6McT"> <Button text="Join Our Discord" link="" /></a>
            </ThemeProvider>
          </ButtonContainer>
        </Box>
      </Container>
      <Showcase>
        
        <Row direction="reverse" ref={Row2Ref}>
          <NftItem img={img5} number={852} price={1} passRef={Row2Ref} />
          <NftItem img={img6} number={123} price={1.2} passRef={Row2Ref} />
          <NftItem img={img7} number={456} price={2.5} passRef={Row2Ref} />
        </Row>
      </Showcase>
      
    </Section>
  );
};

export default About;
